import './style.css'
import * as THREE from 'three';
import {OrbitControls} from 'three/examples/jsm/controls/OrbitControls.js';
import {GLTFLoader} from 'three/examples/jsm/loaders/GLTFLoader.js';

console.log("Hello World!");
var camera, canvas, scene, renderer, effect, model, cube;
var group;
const clock = new THREE.Clock();



function init(){
    const importedModel = new URL('../static/assets/Tanishq.glb', import.meta.url);
    const sizes = {
        width: window.innerWidth,
        height: window.innerHeight
    }
    
    canvas = document.querySelector('.webgl');
    renderer = new THREE.WebGLRenderer({
        canvas: canvas,
        alpha: true
    });
    renderer.setClearColor(0xA3A3A3);
    renderer.setSize(sizes.width, sizes.height - 120);
    
    scene = new THREE.Scene();
    camera = new THREE.PerspectiveCamera(
        45,
        sizes.width / sizes.height,
        0.1,
        1000
    );
    // const orbit = new OrbitControls(camera,renderer.domElement);
    
    camera.position.set(10,10,10);
    // orbit.update();
    
    const grid = new THREE.GridHelper(30,30);
    scene.add(grid);
    
    const assetLoader = new GLTFLoader();

    assetLoader.load(importedModel.href,function(gltf){
        console.log(gltf);
        model = gltf.scene;
        scene.add(model);
        camera.lookAt(model.children[0].position);
    }, undefined, function(error){
        console.error(error);
    });
    
}

function animate(){
    const elapsedTime = clock.getElapsedTime();

    // loading is an sync function so wait for the the model to load
    if(model)
        model.rotation.y = elapsedTime;
    renderer.render(scene,camera);
}

window.addEventListener('resize',function(){
    camera.aspect = window.innerWidth / window.innerHeight;
    camera.updateProjectionMatrix();;
    renderer.setSize(window.innerWidth,window.innerHeight);
});

init();
renderer.setAnimationLoop(animate);









